<template>
    <div>
        <!-- 顶部的筛选 -->
        <classify :iden="1"></classify>

        <!-- 主题内容 -->
        <common :loading="loading" :error="error" :datas="pictureData"></common>
    </div>
</template>
<script>
    import Classify from '../component/classify.vue';
    import Common from '../component/common.vue';
    export default {
        data() {
            return {
                pictureData: []
            }
        },
        created() {
            this.getPictureOplace();
        },
        methods: {

            // 获取图片作品信息
            getPictureOplace() {
                this.error = this.pictureData = null;
                this.loading = true;
                this.$axios.get('/api/picture').then(res => {
                    this.loading = false;
                    this.pictureData = res.data.data;
                }).catch(err => {
                    this.error = err.toString();
                    throw new Error("this.$axios.get: 无法获取数据");
                });
            }
        },
        components: {
            Classify,
            Common
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    .column {
        -webkit-column-count: 3;
        column-count: 3;
        -webkit-column-gap: 15px;
        column-gap: 15px;
    }
</style>



